import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import '../main.css';
import '../css/education.css';

class Education extends Component {
  render() {
    return (
      <div>

      <nav>
        <ul className="navbar">
          <li className="navbar buttonwidth1">
          <Link to={'/'} id="idabout"> </Link>
          </li>
          <li className="navbar buttonwidth2">
          <Link to={'/education'} className='selected' ></Link>
          </li>
          <li className="navbar buttonwidth3">
            <Link to={'/experience'}> </Link>
          </li>
        </ul>
      </nav>
      <div className="hr-arrow-top"></div>

      <div className="margin">
        <div className="profiletext margin">
          <div className="padding-bottom">
            <div className="table-cell bolded minwidth">2012 - 2015</div>
            <div className="table-cell">  
            ,      -. <br/> :    .</div>
          </div>
          <div className="padding-bottom">
            <div className="table-cell bolded minwidth">2003 - 2009</div>
            <div className="table-cell"> . ..  ().  &Prime;&Prime; (   ).
            :     . .</div>
          </div>
          <div>
            <div className="table-cell bolded minwidth"></div>
            <div className="table-cell">   : htmlacademy, codecademy, egghead, javascript, itvdn, codewars</div>
          </div>
        </div>

      </div>      
      <br/>
      </div>
    )
  }
}

export default Education;



